import React from 'react'
import Helmet from 'react-helmet'
import { Link, graphql } from 'gatsby'
import get from 'lodash/get'
import Layout from '../components/layout'
import { rhythm, scale } from '../utils/typography'

class BlogPostTemplate extends React.Component {
  render() {
    const { data, pageContext, location } = this.props
    const { previous, next } = pageContext
    const { href } = location
    const post = data.markdownRemark
    const title = post.frontmatter.title
    const siteTitle = get(this.props, 'data.site.siteMetadata.title')
    const siteDescription = post.excerpt

    return (
      <Layout location={this.props.location}>
        <Helmet
          htmlAttributes={{ lang: 'en' }}
          meta={[{ name: 'description', content: siteDescription }]}
          title={`${title} | ${siteTitle}`}
          script={[
            ADSENSE_SCRIPT_1,
            ADSENSE_SCRIPT_2,
            getDisqusScript(href, title),
          ]}
          noscript={[DISQUS_NOSCRIPT]}
        />
        <h3 style={{ lineHeight: rhythm(1.1) }}>{post.frontmatter.title}</h3>
        <p
          style={{
            ...scale(-1 / 5),
            display: 'block',
            marginBottom: rhythm(3),
            marginTop: rhythm(-1),
            color: '#bbb',
          }}
        >
          {post.frontmatter.date}
        </p>
        <div dangerouslySetInnerHTML={{ __html: post.html }} />
        <div id="disqus_thread" />
        <hr
          style={{
            marginBottom: rhythm(1),
          }}
        />
        <ul
          style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-between',
            listStyle: 'none',
            padding: 0,
            margin: 0,
            fontSize: rhythm(0.5),
          }}
        >
          <li style={{ width: 280 }}>
            {previous && (
              <Link to={previous.fields.slug} rel="prev">
                ← {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li style={{ width: 280 }}>
            {next && (
              <Link to={next.fields.slug} rel="next">
                {next.frontmatter.title} →
              </Link>
            )}
          </li>
        </ul>
      </Layout>
    )
  }
}

const ADSENSE_SCRIPT_1 = {
  async: true,
  src: '//pagead2.googlesyndication.com/pagead/js/adsbygoogle.js',
}
const ADSENSE_SCRIPT_2 = {
  innerHTML: `(adsbygoogle = window.adsbygoogle || []).push({
    google_ad_client: "ca-pub-4588610260101909",
    enable_page_level_ads: true
  });`,
}
const DISQUS_NOSCRIPT = {
  innerHTML: `Please enable JavaScript to view the{' '}
  <a href="https://disqus.com/?ref_noscript">
    comments powered by Disqus.
  </a>`,
}
function getDisqusScript(href, title) {
  return {
    innerHTML: `var disqus_config = function() {
      this.page.url = ${href} // Replace PAGE_URL with your page's canonical URL variable
      this.page.identifier = ${title} // Replace PAGE_IDENTIFIER with your page's unique identifier variable
    }
    ;(function() {
      // DON'T EDIT BELOW THIS LINE
      var d = document,
      s = d.createElement('script')
      s.src = 'https://bonogithub.disqus.com/embed.js'
      s.setAttribute('data-timestamp', +new Date())
      ;(d.head || d.body).appendChild(s)
    })()`,
  }
}

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
      }
    }
  }
`

export default BlogPostTemplate
